  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #2fa8cc;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 20px;
  font-size: 14px;
}

img {
  cursor: pointer;
  transition: all 0.5s;
}

#formContainer {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  justify-content: center;
  align-items: center;
}

div, li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 2.5rem;
  border-radius: 4px;
  border: 0;
  padding: 0.1rem;
  align-self: stretch;
}

li {
  justify-content: center;
}
li .onEdit {
  background-color: rgb(253, 227, 178);
}

div h1 {
  color: black;
  font-size: 1.2rem;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  letter-spacing: 0.0025rem;
}
div #userInput {
  width: 100%;
}

input {
  height: 2rem;
  border: 0;
  padding: 0.3rem;
  align-self: stretch;
  border-radius: 2px;
}

#checkB {
  border-radius: 4px;
  width: 5vw;
  align-self: center;
}

.container {
  background-color: #f4f4f4;
  border-radius: 4px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 15px 5px;
  text-align: left;
  max-width: 100%;
  width: 800px;
}

img {
  height: 1.5rem;
  transition: all 0.2s;
}

#recyclImg {
  opacity: 0.5;
}
#recyclImg:hover {
  opacity: 1;
}

#dotsImg {
  opacity: 7;
}
#dotsImg::active {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100%;
  background-repeat: no-repeat;
}

#clearBtn {
  transition: all 0.5s;
  border-radius: 2px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 0.001em;
  word-spacing: normal;
  background-color: #2fa8cc;
  color: #f4f4f4;
  border: 0;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  cursor: pointer;
  text-align: center;
  width: 100%;
}
#clearBtn:active {
  transform: scale(0.98);
}
#clearBtn:focus {
  outline: 0;
